                </div>
                <div class="nav-item">
                    <div class="icon-img">
                        <img :src="this.qianbao" alt="">
                    </div>
                    <p class="navname">E</p>
                </div>
                <div class="nav-item" @click="routerPath('socialsecurity')">
                    <div class="icon-img">
                            <img :src="this.gongjijin" alt="">
                        </div>
