        <!--  -->
        <div class="notice">
            <div class="notice-box">
                <div class="notice-box-left">
                    <img :src="this.tongzhi" alt="">
                </div>
                <div class="notice-box-mid">
                    <img :src="this.guangbo" alt="">
                </div>
                <div class="notice-box-right">
                    <ul>
                        <li>
                            <span>8.2</span>201920192019
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="lifeservice">
            <div class="title">
                <span class="blueblock"></span>
                <span class="title-value"></span>
            </div>
            <div class="service">
                <div class="service-item" @click="routerPath('livingpayment')">
                    <div class="item-left">
                        <img :src="this.jiaofei" alt="">
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
                <div class="service-item" @click="goModule('http://zntcdemo.zhas.huichifangqiu.com')">
                    <div class="item-left">
                        <img :src="this.park" alt="">
                        <span></span>
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
                <div class="service-item" @click="goModule('http://zhgjdemo.zhas.huichifangqiu.com')">
                    <div class="item-left">
                        <img :src="this.gongjiao" alt="">
                        <span></span>
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
                <div class="service-item" @click="goModule('https://weixincs.guizhou12320.org.cn/12320resources/webchat/index.html')">
                    <div class="item-left">
                        <img :src="this.guahao" alt="">
                        <span></span>
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="specialhelp">
            <div class="title">
                <span class="blueblock"></span>
                <span class="title-value"></span>
            </div>
            <div class="service">
                <div class="service-item" @click="goModule('http://swzldemo.zhas.huichifangqiu.com')">
                    <div class="item-left">
                        <img :src="this.shiwu" alt="">
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
                <div class="service-item" @click="goModule('http://wzpzdemo.zhas.huichifangqiu.com')">
                    <div class="item-left">
                        <img :src="this.weizhang" alt="">
                        <span></span>
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
                <div class="service-item" @click="goModule('http://shgydemo.zhas.huichifangqiu.com')">
                    <div class="item-left">
                        <img :src="this.gongyi" alt="">
                        <span></span>
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
                <div class="service-item" @click="goModule('http://czgcdemo.zhas.huichifangqiu.com')">
                    <div class="item-left">
                        <img :src="this.gongce" alt="">
                        <span></span>
                    </div>
                    <div class="item-right">
                        <p class="item-name"></p>
                        <p class="item-depict"></p>
                    </div>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="cityservice">
            <div class="title">
                <span class="blueblock"></span>
                <span class="title-value"></span>
            </div>
            <div class="list-box">
                <ul class="list-ul">
                    <li class="list-item">
                        <button  @click="goModule('http://bdxtdemo.zhas.huichifangqiu.com/')">
                            <img :src="this.biyesheng" alt="">
                            <p></p>
                            <i class="hot-icon"></i>
                        </button>
                    </li>
                    <li class="list-item">
                        <button @click="goModule('http://zbxtm.inanshun.com')">
                            <img :src="this.zhengbing" alt="">
                            <p></p>
                            <i class="hot-icon"></i>
                        </button>
                    </li>
                    <li class="list-item">
                        <button>
                            <img :src="this.network" alt="">
                            <p></p>
                        </button>
                    </li>
                    <li class="list-item">
                        <button>
                            <img :src="this.network" alt="">
                            <p></p>
                        </button>
                    </li>
                </ul>
            </div>
        </div>
        <!--  -->
        <div class="huiservice">
            <div class="title">
                <span class="blueblock"></span>
                <span class="title-value"></span>
                <span class="gonghang"></span>
            </div>
            <div class="huiservice-box">
                <div class="huiservice-item">
                    <img :src="this.xinyongka" alt="">
                </div>
                <div class="huiservice-item">
                    <img :src="this.shebaoka" alt="">
                </div>
            </div>
        </div>
        <div class="no-more">
            <img :src="this.no_more" alt="">
        </div>
        <FooterBar></FooterBar>
        <div id="container"></div>
    </div>
</template>

<script>
import wx from 'weixin-js-sdk'
import { bannerList , Login , SDKconfig , getMeinfo } from '@/api/api.js'
import FooterBar from'../../components/FooterBar'
export default {
    name: 'home',
    components:{
        FooterBar
    },
    data () {
        return {
            WeatherInfo: {},
            isLoading: true,
            saoyisao: require('@/assets/image/saoyisao.png'),
            qianbao: require('@/assets/image/qianbao.png'),
            tongzhi: require('@/assets/image/tongzhi.png'),
            guangbo: require('@/assets/image/guangbo.png'),
            gongjijin: require('@/assets/image/gongjijin.png'),
            jiaofei: require('@/assets/image/jiaofei.png'),
            park: require('@/assets/image/park.png'),
            gongjiao: require('@/assets/image/gongjiao.png'),
            guahao: require('@/assets/image/guahao.png'),
            biyesheng: require('@/assets/image/biyesheng.png'),
            zhengbing: require('@/assets/image/zhengbing.png'),
            network: require('@/assets/image/network.png'),
            xinyongka: require('@/assets/image/xinyongka.png'),
            shebaoka: require('@/assets/image/shebaoka.png'),
            no_more: require('@/assets/image/no_more.png'),
            gongyi: require('@/assets/image/gongyi.png'),
            gongce: require('@/assets/image/gongce.png'),
            shiwu: require('@/assets/image/shiwu.png'),
            weizhang: require('@/assets/image/weizhang.png'),
            weatherIcon: require('@/assets/weather/yin.png'),
            weather: {
                qing: require('@/assets/weather/qing.png'),
                dayu: require('@/assets/weather/dayu.png'),
                xiaoyu: require('@/assets/weather/xiaoyu.png'),
                wu: require('@/assets/weather/wu.png'),
                xue: require('@/assets/weather/xue.png'),
                yin: require('@/assets/weather/yin.png')
            }
        }
    },
    created(){
        this.Mloading = true;
        SDKconfig().then(res => {
            wx.config({
                debug: false,// ,apialert
                appId: res.data.appId,// 
                timestamp: res.data.timestamp,// 
                nonceStr: res.data.nonceStr, //
                signature: res.data.signature, //
                jsApiList: ["openLocation" , "getLocation"]// JS
            });
            let self = this;
            wx.ready(function(){
                wx.getLocation({
                    type: 'gcj02',
                    success: function (res) {
                        // console.log('',res);
                        self.loginInfo(res.longitude,res.latitude);
                    },
                    fail:function(){
                        console.log('');
                    }
                });
            })
        }).catch(error => {
            console.log(error);
        });
        this.nowTime = this.getNowDate()
    },
    mounted(){
        this.map = new AMap.Map('container', {
            resizeEnable: true,
            zoom: 12,
        });
        let _this = this
        AMap.plugin('AMap.Weather', function() {
            let weather = new AMap.Weather();
            //,  
            weather.getLive('', function(err, data) {
                // console.log('',data,data.weather)
                let weatherStr = data.weather;
                if(weatherStr.indexOf("")!=-1){
                    _this.weatherIcon = _this.weather.qing
                }else if(weatherStr.indexOf("")!=-1){
                    _this.weatherIcon = _this.weather.xiaoyu
                }else if(weatherStr.indexOf("")!=-1 || weatherStr.indexOf("")!=-1){
                    _this.weatherIcon = _this.weather.dayu
                }else if(weatherStr.indexOf("")!=-1){
                    _this.weatherIcon = _this.weather.xue
                }else{
                    _this.weatherIcon = _this.weather.yin
                }

                if (!err) {
                    _this.WeatherInfo = data
                }
            })
        })
    },
    methods: {
        getNowDate () {
            let oDate = new Date(); //
            let year = oDate.getFullYear();   //
            let month = oDate.getMonth()+1;   //01
            let day = oDate.getDate(); // 
            // oDate.getHours(); //
            // oDate.getMinutes(); //
            // oDate.getSeconds(); //
            let time = year+'/'+month+'/'+day;
            return time;
        },

        goModule (url) {
            location.href = url;
        },

         // 
        routerPath (name) {
            this.$router.push({
                name: name
            })
        },

        delay(){
            setTimeout(() => {
                this.isLoading = false;
            }, 500);
        },

        loginInfo(lon,lat){
            Login(String(lon),String(lat)).then(res => {
                // console.log('',res);
                this.delay();
                sessionStorage.setItem("meInfo",JSON.stringify(res.data));
            }).catch(error => {
                console.log(error);
            });
        },
    }
}
</script>


